@media screen and (max-width: 576px) {
  .myIcon {
    position: absolute;
    right: -50px;
    top: -100px;
    transform: scale(0.5);
  }

  .parallax{
    background-size: contain;
    min-height: 280px; 
  }
}

@media screen and (min-width: 576px) {
  .parallax{
    min-height: 480px; 
    background-attachment: fixed;
  }
}
    



.parallax {
    background-image: url("https://i.pinimg.com/originals/c9/72/f0/c972f0909879d3ce4137c7140e26922c.jpg");
    background-position: center;
    background-repeat: no-repeat;
    // background-size: cover;
  }